/** @jsx jsx */
import React from "react"
import { jsx, useThemeUI } from "theme-ui"
import { CheckSquareFill } from "react-bootstrap-icons"
import getPaymentUrl from "Components/payments/qiwi"

export default function BuyModal(props) {
  const context = useThemeUI()
  const { theme } = context
  let clientEmail = ""
  let productAmount = 1
  let productDiscount = 0
  const node = props.data

  function getFinalPrice(price, discount, amount) {
    let priceWithDiscount = (price / 100) * discount
    let finalPrice = (price - priceWithDiscount) * amount
    return finalPrice
  }

  return (
    <div
      className="modal fade"
      id={node.id.replace(/[.,"*+!-?^${}()|[\]\\/]/gu, "")}
      tabIndex="-1"
      aria-hidden="true"
    >
      <div className="modal-dialog modal-dialog-centered modal-sm">
        <div className="modal-content">
          <form
            sx={{
              backgroundColor: "background_primary",
              borderRadius: `${theme.root.secondaryBorderRadius}`,
            }}
            onSubmit={event => {
              event.preventDefault()
              if (clientEmail.indexOf("@") !== -1) {
                const paymentInfo = {
                  amount: getFinalPrice(
                    node.frontmatter.extra.price,
                    productDiscount,
                    productAmount
                  ),
                  email: clientEmail,
                  comment: ``,
                }
                location.href = getPaymentUrl(paymentInfo)
              }
            }}
          >
            <div
              className={"element element-large"}
              sx={{
                boxShadow: `inset 0px -1px 0px ${theme.colors.separator_opaque}`,
              }}
            >
              <div className={"element-center-part"}>
                <span>Оформить заказ</span>
              </div>
            </div>
            <div
              sx={{
                paddingY: "24px",
                display: "flex",
                flexDirection: "column",
              }}
            >
              <div className={`element element-medium`}>
                <div className={"element-left-part"}>
                  <input
                    type="email"
                    placeholder="Ваш email"
                    className={`element element-medium element-input input-filled`}
                    sx={{ paddingX: "16px" }}
                    required
                    onChange={event => {
                      clientEmail = event.target.value
                    }}
                  />
                </div>
              </div>
              {node.frontmatter.extra.amount > 1 && (
                <div
                  className={`element element-medium`}
                  sx={{ marginTop: "8px" }}
                >
                  <div className={"element-left-part"}>
                    <input
                      placeholder={`Количество товара (макс. ${node.frontmatter.extra.amount})`}
                      className={`element element-medium element-input input-filled`}
                      sx={{ paddingX: "16px" }}
                      required
                      onChange={event => {
                        productAmount = event.target.value
                      }}
                    />
                  </div>
                </div>
              )}
              <div
                className={`element element-medium`}
                sx={{ marginTop: "8px" }}
              >
                <div className={"element-left-part"}>
                  <input
                    type="text"
                    placeholder="Купон на скидку (не обязательно)"
                    className={`element element-medium element-input input-filled`}
                    sx={{ paddingX: "16px" }}
                    onChange={event => {
                      if (event.target.value.toUpperCase() === "POMOIKA") {
                        productDiscount = 10
                      }
                    }}
                  />
                </div>
              </div>
              <div sx={{ marginTop: "24px" }}>
                <div
                  className={"element element-small"}
                  sx={{
                    textTransform: "uppercase",
                    color: "text_secondary",
                    boxShadow: `0px 1px 0px ${theme.colors.separator_opaque}`,
                  }}
                >
                  <div className={"element-left-part"}>
                    <span>Способ оплаты</span>
                  </div>
                </div>

                <div className={`element element-medium`}>
                  <div
                    className={"element-left-part"}
                    sx={{
                      color: "text_primary",
                      i: {
                        marginRight: "8px !important",
                      },
                    }}
                  >
                    <i>
                      <CheckSquareFill size={18} sx={{ color: "tint_blue" }} />
                    </i>
                    <span>Банковская карта или Qiwi</span>
                  </div>
                </div>
              </div>
              <div sx={{ marginTop: "24px" }}>
                <div
                  className={"element element-small"}
                  sx={{
                    color: "text_secondary",
                    boxShadow: `0px -1px 0px ${theme.colors.separator_opaque}`,
                    paddingY: "8px",
                    paddingX: "16px",
                  }}
                >
                  <span>
                    Нажимая на кнопку "Оплатить" вы соглашаетесь с{" "}
                    <a href={"/rules"} target={"blank"}>
                      правилами магазина
                    </a>
                  </span>
                </div>
              </div>
            </div>
            <div
              sx={{
                display: "flex",
                boxShadow: `inset 0px 1px 0px ${theme.colors.separator_opaque}`,
              }}
            >
              <button
                type="button"
                data-dismiss="modal"
                aria-label="Close"
                className={"element element-large element-button"}
                sx={{
                  width: "100%",
                  justifyContent: "center",
                  color: "tint_blue",
                  paddingX: "16px",
                  boxShadow: `inset -1px 0px 0px ${theme.colors.separator_opaque}`,
                  borderRadius: `0px !important`,
                }}
              >
                Отмена
              </button>
              <button
                type={"submit"}
                className={"element element-large element-button"}
                sx={{
                  width: "100%",
                  justifyContent: "center",
                  paddingX: "16px",
                  color: "tint_blue",
                  fontWeight: "bold",
                  ":hover": {
                    backgroundColor: "transparent",
                  },
                }}
              >
                Оплатить
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  )
}
