/** @jsx jsx */
import React from "react"
import { graphql } from "gatsby"
import Img from "gatsby-image"
import { MDXRenderer } from "gatsby-plugin-mdx"
import { jsx, useThemeUI } from "theme-ui"
import Layout from "Project/_components/layouts/001"
import { ChevronRight, CheckSquareFill } from "react-bootstrap-icons"
import getPaymentUrl from "Components/payments/qiwi"
import BuyModal from "Project/_components/buyModal"

export default function ShopProduct({ data }) {
  const context = useThemeUI()
  const { theme } = context
  const node = data.mdx
  const extra = data.mdx.frontmatter.extra
  let clientEmail = ""
  let byProductAmount = 1

  return (
    <>
      <Layout>
        <div
          sx={{
            padding: "16px",
            "h1, h2, h3": {
              marginBottom: "16px",
              marginTop: "16px",
            },
          }}
        >
          <div
            sx={{
              display: "grid",
              gridTemplateColumns: "2fr 4fr ",
              gridGap: "16px",
              gridTemplateAreas: `"product-title product-title"\n"product-image \nproduct-info"`,
              "@media (max-width: 1200px)": {
                gridTemplateAreas: `"product-title product-title"\n"product-image ."\n"product-info product-info"`,
              },
            }}
          >
            <h1 sx={{ gridArea: "product-title" }}>{node.frontmatter.title}</h1>

            <Img
              alt={`${node.frontmatter.title} cover image`}
              fluid={node.frontmatter.cover.childImageSharp.fluid}
              sx={{
                gridArea: "product-image",
                minWidth: "288px",
                maxWidth: "600px",
                borderRadius: `${theme.root.secondaryBorderRadius}`,
              }}
            />

            <div sx={{ gridArea: "product-info" }}>
              <div
                className={"element element-small"}
                sx={{ textTransform: "uppercase", color: "text_secondary" }}
              >
                <div className={"element-left-part"}>
                  <span>Описание</span>
                </div>
              </div>
              <div sx={{ minWidth: "360px" }}>
                <div
                  sx={{
                    backgroundColor: "background_primary",
                    boxShadow: `0px 1px 0px ${theme.colors.separator_opaque}, 0px -1px 0px ${theme.colors.separator_opaque}, 1px 0px 0px ${theme.colors.separator_opaque}, -1px 0px 0px ${theme.colors.separator_opaque}`,
                    marginBottom: "16px",
                    borderRadius: `${theme.root.secondaryBorderRadius}`,
                    ".element:first-child": {
                      borderRadius: `${theme.root.secondaryBorderRadius} ${theme.root.secondaryBorderRadius} 0 0`,
                    },
                    ".element:last-child": {
                      borderRadius: `0 0 ${theme.root.secondaryBorderRadius} ${theme.root.secondaryBorderRadius}`,
                    },
                    ".element:hover": {
                      backgroundColor: "fill_quarternary",
                    },
                  }}
                >
                  {extra.link !== null && (
                    <div>
                      <a
                        href={extra.link}
                        target={"blank"}
                        className={"element element-large element-row"}
                      >
                        <div className={"element-left-part"}>
                          <span sx={{ color: "tint_blue" }}>
                            Ссылка на аккаунт
                          </span>
                        </div>
                        <div className={"element-right-part"}>
                          <i>
                            <ChevronRight size={18} sx={{ color: "gray_02" }} />
                          </i>
                        </div>
                      </a>
                    </div>
                  )}
                  {extra.mail !== null && (
                    <div
                      className={"element element-large element-row"}
                      sx={{ width: "100%", cursor: "default !important" }}
                    >
                      <div className={"element-left-part"}>
                        <span>Привязка к почте - </span>
                      </div>
                      <div className={"element-right-part"}>
                        <span>{extra.mail ? "Есть" : "Нет"}</span>
                      </div>
                    </div>
                  )}
                  {extra.phone !== null && (
                    <div
                      className={"element element-large element-row"}
                      sx={{ width: "100%", cursor: "default !important" }}
                    >
                      <div className={"element-left-part"}>
                        <span>Привязка к телефону - </span>
                      </div>
                      <div className={"element-right-part"}>
                        <span>{extra.phone ? "Есть" : "Нет"}</span>
                      </div>
                    </div>
                  )}
                  <div
                    className={"element element-large element-row"}
                    sx={{ width: "100%", cursor: "default !important" }}
                  >
                    <div className={"element-left-part"}>
                      <span>Наличие - </span>
                    </div>
                    <div className={"element-right-part"}>
                      {extra.amount > 1 && (
                        <span sx={{ color: "tint_green" }}>
                          В наличии {extra.amount} шт.
                        </span>
                      )}
                      {extra.amount === 1 && (
                        <span sx={{ color: "tint_green" }}>В наличии</span>
                      )}
                      {extra.amount === null && (
                        <span sx={{ color: "tint_red" }}>Нет в наличии</span>
                      )}
                    </div>
                  </div>

                  {extra.vehicles.x !== null && (
                    <div
                      className={"element element-large element-row"}
                      sx={{ width: "100%", cursor: "default !important" }}
                    >
                      <div
                        className={"element-left-part"}
                        sx={{
                          flexWrap: "wrap",
                          paddingY: "8px",
                          position: "relative !important",
                          ".vehicle-link": {
                            ":not(:last-child):after": {
                              content: `","`,
                              marginLeft: "2px",
                              marginRight: "8px",
                              color: "text_secondary",
                            },
                          },
                        }}
                      >
                        <span
                          sx={{ whiteSpace: "nowrap", marginRight: "16px" }}
                        >{`ТОПы:`}</span>
                        {extra.vehicles.x.map(element => (
                          <span className={"vehicle-link"}>
                            <a
                              key={element}
                              target={"blank"}
                              href={`https://wiki.wargaming.net/ru/index.php?search=${element}`}
                            >
                              {element}
                            </a>
                          </span>
                        ))}
                      </div>
                    </div>
                  )}
                  {extra.vehicles.premium !== null && (
                    <div
                      className={"element element-large element-row"}
                      sx={{ width: "100%", cursor: "default !important" }}
                    >
                      <div
                        className={"element-left-part"}
                        sx={{
                          flexWrap: "wrap",
                          paddingY: "8px",
                          position: "relative !important",
                          ".vehicle-link": {
                            ":not(:last-child):after": {
                              content: `","`,
                              marginLeft: "2px",
                              marginRight: "8px",
                              color: "text_secondary",
                            },
                          },
                        }}
                      >
                        <span
                          sx={{ whiteSpace: "nowrap", marginRight: "16px" }}
                        >{`Премиум техника:`}</span>
                        {extra.vehicles.premium.map(element => (
                          <span className={"vehicle-link"}>
                            <a
                              key={element}
                              target={"blank"}
                              href={`https://wiki.wargaming.net/ru/index.php?search=${element}`}
                            >
                              {element}
                            </a>
                          </span>
                        ))}
                      </div>
                    </div>
                  )}
                </div>
                {/* // */}

                {/* // */}
                {extra.amount !== null && extra.amount !== 0 && (
                  <div
                    sx={{
                      backgroundColor: "background_primary",
                      boxShadow: `0px 1px 0px ${theme.colors.separator_opaque}, 0px -1px 0px ${theme.colors.separator_opaque}, 1px 0px 0px ${theme.colors.separator_opaque}, -1px 0px 0px ${theme.colors.separator_opaque}`,
                      padding: "16px",
                      display: "flex",
                      borderRadius: `${theme.root.secondaryBorderRadius}`,
                    }}
                  >
                    <span
                      className={"element element-large"}
                      sx={{
                        marginLeft: "8px",
                        marginRight: "24px",
                      }}
                    >
                      {`${extra.price} руб.`}
                    </span>
                    <button
                      className={
                        "element element-large element-button button-primary"
                      }
                      data-toggle="modal"
                      data-target={`#${node.id.replace(
                        /[.,"*+!-?^${}()|[\]\\/]/gu,
                        ""
                      )}`}
                      sx={{ paddingX: "24px" }}
                    >
                      Купить
                    </button>
                    <BuyModal data={node}></BuyModal>
                    <div
                      className="modal fade"
                      id={node.id.replace(/[.,"*+!-?^${}()|[\]\\/]/gu, "")}
                      tabIndex="-1"
                      aria-hidden="true"
                    >
                      <div className="modal-dialog modal-dialog-centered modal-sm">
                        <div className="modal-content">
                          <form
                            sx={{
                              backgroundColor: "background_primary",
                              borderRadius: `${theme.root.secondaryBorderRadius}`,
                            }}
                            onSubmit={event => {
                              event.preventDefault()
                              if (clientEmail.indexOf("@") !== -1) {
                                const paymentInfo = {
                                  amount: extra.price * byProductAmount,
                                  email: clientEmail,
                                  comment: ``,
                                }
                                location.href = getPaymentUrl(paymentInfo)
                              }
                            }}
                          >
                            <div
                              className={"element element-large"}
                              sx={{
                                boxShadow: `inset 0px -1px 0px ${theme.colors.separator_opaque}`,
                              }}
                            >
                              <div className={"element-center-part"}>
                                <span>Оформить заказ</span>
                              </div>
                            </div>
                            <div
                              sx={{
                                paddingY: "24px",
                                display: "flex",
                                flexDirection: "column",
                              }}
                            >
                              <div className={`element element-medium`}>
                                <div className={"element-left-part"}>
                                  <input
                                    type="email"
                                    placeholder="Ваш email"
                                    className={`element element-medium element-input input-filled`}
                                    sx={{ paddingX: "16px" }}
                                    required
                                    onChange={event => {
                                      clientEmail = event.target.value
                                    }}
                                  />
                                </div>
                              </div>
                              {extra.amount > 1 && (
                                <div
                                  className={`element element-medium`}
                                  sx={{ marginTop: "8px" }}
                                >
                                  <div className={"element-left-part"}>
                                    <input
                                      placeholder={`Количество товара (макс. ${extra.amount})`}
                                      className={`element element-medium element-input input-filled`}
                                      sx={{ paddingX: "16px" }}
                                      required
                                      onChange={event => {
                                        byProductAmount = event.target.value
                                      }}
                                    />
                                  </div>
                                </div>
                              )}
                              <div sx={{ marginTop: "24px" }}>
                                <div
                                  className={"element element-small"}
                                  sx={{
                                    textTransform: "uppercase",
                                    color: "text_secondary",
                                    boxShadow: `0px 1px 0px ${theme.colors.separator_opaque}`,
                                  }}
                                >
                                  <div className={"element-left-part"}>
                                    <span>Способ оплаты</span>
                                  </div>
                                </div>

                                <div className={`element element-medium`}>
                                  <div
                                    className={"element-left-part"}
                                    sx={{
                                      color: "text_primary",
                                      i: {
                                        marginRight: "8px !important",
                                      },
                                    }}
                                  >
                                    <i>
                                      <CheckSquareFill
                                        size={18}
                                        sx={{ color: "tint_blue" }}
                                      />
                                    </i>
                                    <span>Банковская карта или Qiwi</span>
                                  </div>
                                </div>
                              </div>

                              <div sx={{ marginTop: "24px" }}>
                                <div
                                  className={"element element-small"}
                                  sx={{
                                    color: "text_secondary",
                                    boxShadow: `0px -1px 0px ${theme.colors.separator_opaque}`,
                                    paddingY: "8px",
                                    paddingX: "16px",
                                  }}
                                >
                                  <span>
                                    Нажимая на кнопку "Оплатить" вы соглашаетесь
                                    с{" "}
                                    <a href={"/rules"} target={"blank"}>
                                      правилами магазина
                                    </a>
                                  </span>
                                </div>
                              </div>
                            </div>
                            <div
                              sx={{
                                display: "flex",
                                boxShadow: `inset 0px 1px 0px ${theme.colors.separator_opaque}`,
                              }}
                            >
                              <button
                                type="button"
                                data-dismiss="modal"
                                aria-label="Close"
                                className={
                                  "element element-large element-button"
                                }
                                sx={{
                                  width: "100%",
                                  justifyContent: "center",
                                  color: "tint_blue",
                                  paddingX: "16px",
                                  boxShadow: `inset -1px 0px 0px ${theme.colors.separator_opaque}`,
                                  borderRadius: "0px !important",
                                }}
                              >
                                Отмена
                              </button>
                              <button
                                type={"submit"}
                                className={
                                  "element element-large element-button"
                                }
                                sx={{
                                  width: "100%",
                                  justifyContent: "center",
                                  paddingX: "16px",
                                  color: "tint_blue",
                                  fontWeight: "bold",
                                  ":hover": {
                                    backgroundColor: "transparent",
                                  },
                                }}
                              >
                                Оплатить
                              </button>
                            </div>
                          </form>
                        </div>
                      </div>
                    </div>
                  </div>
                )}
              </div>
            </div>
          </div>
          {node.excerpt !== null && (
            <div sx={{ marginTop: "32px" }}>
              <MDXRenderer>{node.body}</MDXRenderer>
            </div>
          )}
        </div>
      </Layout>
    </>
  )
}

export const query = graphql`
  query($slug: String!) {
    mdx(slug: { eq: $slug }) {
      body
      excerpt
      frontmatter {
        title
        slug
        cover {
          childImageSharp {
            fluid(maxWidth: 600) {
              ...GatsbyImageSharpFluid_withWebp
            }
          }
        }
        extra {
          amount
          price
          link
          phone
          mail
          vehicles {
            x
            premium
          }
        }
      }
      id
    }
  }
`
